<template>
  <section v-if="details.length != 0" class="details">
    <div
      style="marginTop: 50px; padding: 15px"
      :class="direction === 'ltr' ? 'row' : 'row text-right'"
    >
      <div class="col-12 col-sm-6 d-flex flex-column align-items-center">
        <DetailSlider :images="details.images" />
      </div>
      <div class="col-12 col-sm-6">
        <h2 class="text-capitalize title">
          {{ details.title }}
        </h2>
        <p class="text-muted">
          {{ langTranslations.data[0].details.madeby }}: {{ details.company }}
        </p>
        <p class="text-danger">
          {{ langTranslations.data[0].details.price }}: {{ details.price }} $
        </p>
        <p style="textDecoration: underline">
          {{ langTranslations.data[0].details.info }}:
        </p>
        <p>{{ details.info }}</p>
        <div style="d-flex">
          <span
            v-for="color in details.colors"
            :key="color"
            :style="{ background: color }"
            class="color"
          />
        </div>
        <div>
          <router-link class="btn btn-outline-danger" to="/products">
            {{ langTranslations.data[0].details.backBtn }}
          </router-link>
          <button
            class="btn btn-outline-info"
            :disabled="details.inCart"
            style="margin: 25px"
            @click="showModal(details.id)"
          >
            {{
              details.inCart
                ? langTranslations.data[0].details.inCart
                : langTranslations.data[0].details.addTo
            }}
          </button>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import DetailSlider from "@/components/details/DetailSlider";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Detials",
  components: { DetailSlider },
  computed: mapGetters(["direction", "langTranslations", "details"]),
  methods: mapActions(["showModal"])
};
</script>

<style scoped>
.title {
  margin: 25px 0;
  font-family: fanatsy, DroidKufi-Bold;
}
.color {
  width: 20px;
  height: 20px;
  margin: 5px;
  display: inline-block;
  border: 1px solid;
  border-radius: 3px;
}
</style>
