<template>
  <section
    v-if="modalOpen"
    class="modal-section"
    @click="closeModal"
    :class="direction === 'ltr' ? '' : 'text-right'"
  >
    <div
      class="modal-container col-sm-6 col-12"
      @click="e => e.stopPropagation()"
    >
      <p class="loading-title"> {{ langTranslations.data[0].modalbox.loading }} <span id="points"></span></p>
      <p class="title added-title">
        {{ langTranslations.data[0].modalbox.title }}
      </p>
      <div class="fadeIn">
        <img
          :src="`/${details.images[0]}`"
          alt="details"
          class="img img-thumbnail"
        />
        <div>
          <h5>{{ details.title }}</h5>
          <p class="text-center text-danger">{{ details.price }} $</p>
        </div>
        <div class="d-flex justify-content-between" style="width:100%">
          <router-link to="/basket">
            <button class="btn btn-outline-danger" @click="closeModal">
              <i class="fa fa-shopping-cart" style="margin: 5px" />
              {{ langTranslations.data[0].modalbox.basketBtn }}
            </button>
          </router-link>
          <router-link to="/products">
            <button class="btn btn-outline-info" @click="closeModal">
              {{ langTranslations.data[0].modalbox.continue }}
            </button>
          </router-link>
        </div>
      </div>
      <div class="move-container">
        <img :src="`/${details.images[0]}`" alt="details" class="thumb" />
        <img src="/images/icons/basket.png" alt="basket" class="thumb" />
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "PopUpModal",
  computed: mapGetters([
    "direction",
    "langTranslations",
    "details",
    "modalOpen"
  ]),
  methods: mapActions(["closeModal"]),
  updated() {
    const pointsSpan = document.getElementById("points");
    let timer;
    if (pointsSpan) {
      timer = setInterval(function() {
        if (pointsSpan.textContent === "...") {
          pointsSpan.textContent = "";
        }
        pointsSpan.textContent += ".";
      }, 200);
    }
    setTimeout(function() {
      clearInterval(timer);
    }, 1500);
  }
};
</script>

<style scoped>
.modal-section {
  position: fixed;
  z-index: 1111111;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-container {
  position: relative;
  width: 40%;
  height: 90%;
  padding: 50px;
  background-color: white;
  border-radius: 5px;
}
.fadeIn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 30px 0;
  opacity: 0;
  animation: fadeIn 0.4s 2.2s 1 forwards;
}
.img {
  width: 100px;
  height: 150px;
  max-height: 80%;
  margin-bottom: 15px;
  border-radius: 5px;
}
.title {
  padding: 10px;
  border-radius: 5px;
  color: white;
  background-color: #484b4d;
  z-index: 10000;
  position: relative;
}
.loading-title {
  animation: loading 1.5s 1 forwards;
}
@keyframes loading {
  0% {
    opacity: 1;
  }
  99% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.added-title {
  animation: added 1.9s 1 forwards;
  margin-top: -50px;
}
@keyframes added {
  0% {
    opacity: 0;
  }
  95% {
    opacity: 0;
  }
  99% {
    opacity: 1;
  }
}
.move-container {
  position: absolute;
  top: 90px;
  height: 100px;
  width: 95%;
  padding-top: 50px;
  background: white;
  animation: fadeOut 0.4s 2.2s 1 forwards;
}
.thumb {
  width: 100px;
  height: 100px;
  position: absolute;
}
.thumb:first-child {
  left: 10%;
  animation: moveToBasket 1.5s 0.4s 1 ease-in-out forwards;
}
.thumb:last-child {
  right: 8%;
  animation: brightness 0.4s 1.7s 1 ease-in-out forwards;
}
@keyframes moveToBasket {
  0% {
    left: 10%;
  }
  50% {
    transform: scale(1.3);
  }
  75% {
    opacity: 1;
  }
  100% {
    left: 70%;
    opacity: 0;
  }
}
@keyframes brightness {
  0% {
    filter: brightness(1);
    filter: brightness(1);
  }
  50% {
    filter: brightness(2);
    filter: brightness(2);
  }
  100% {
    filter: brightness(1);
    filter: brightness(1);
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    z-index: -9;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes flash {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@media (max-width: 400px) {
  button {
    font-size: 0.8em;
  }
}
</style>
