<template>
  <div id="app">
    <Navbar v-if="langTranslations.data" />
    <Loading v-if="isLoading" />
    <div class="main-view" v-else-if="langTranslations.data">
      <router-view />
      <PopUpModal />
      <ClearBasketModal />
      <Contact />
      <ScrollToTop />
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Navbar from "@/components/navbar/Navbar";
import Loading from "@/components/Loading";
import PopUpModal from "@/components/products/PopUpModal";
import ClearBasketModal from "@/components/basket/ClearBasketModal";
import Contact from "@/components/Contact";
import ScrollToTop from "@/components/ScrollToTop";
export default {
  name: "App",
  components: {
    Navbar,
    Loading,
    PopUpModal,
    ClearBasketModal,
    Contact,
    ScrollToTop
  },
  computed: mapGetters(["langTranslations", "isLoading"]),
  methods: mapActions(["fetchData", "closeModal", "hideContact"]),
  updated() {
    const scrollUpBtn = document.getElementById("scrollUpBtn");
    window.onscroll = function() {
      if (window.pageYOffset > 400) {
        scrollUpBtn.style.opacity = 1;
        scrollUpBtn.style.cursor = "pointer";
      } else {
        scrollUpBtn.style.opacity = 0;
        scrollUpBtn.style.cursor = "auto";
      }
    };
  },
  created() {
    this.fetchData();
    window.addEventListener("keydown", e => {
      if (e.which === 27) {
        this.hideContact();
        this.closeModal();
      }
    });
  }
};
</script>

<style scoped>
.main-view {
  padding: 90px 10px 0;
  overflow: hidden;
}
@media (max-width: 400px) {
  .main-view {
    padding: 90px 5px 0;
    font-size: 0.8em !important;
  }
}
</style>
