<template>
  <section class="products">
    <h5 class="title" style="text-align: center">
      {{ langTranslations.data[0].products.title }}
    </h5>
    <div class="tabs-container">
      <div class="tabs-btns">
        <button
          :class="activeBtn === 'all' ? 'active' : null"
          @click="chooseAll"
          style="background: #afafaf"
        >
          {{ langTranslations.data[0].products.allBtn }}
        </button>
        <button
          v-for="cat in categories"
          :key="cat"
          :class="activeBtn === cat ? 'cat-buttons active' : 'cat-buttons'"
          @click="chooseCat(cat)"
        >
          {{ cat }}
        </button>
      </div>
    </div>
    <div class="row">
      <div class="range col-lg-6">
        <span>{{ minPrice }}$</span>
        <input
          type="range"
          step="1"
          :min="minPrice"
          :max="maxPrice"
          :value="rangeValue"
          @input="e => setRangeValue(e.currentTarget.value)"
        />
        <span>{{ maxPrice }}$</span>
        <span class="range-value"
          >{{ langTranslations.data[0].products.maxPrice }}:
          {{ rangeValue }}$</span
        >
      </div>
      <div class="range col-lg-6">
        <div style="line-height: 1px">
          <input
            id="acc"
            type="radio"
            value="acc"
            name="orderPrice"
            @change="e => changeOrderPrice(e.currentTarget.value)"
          />
          <label for="acc">{{ langTranslations.data[0].products.acc }}</label>
        </div>
        <div style="line-height: 1px">
          <input
            id="desc"
            type="radio"
            value="desc"
            name="orderPrice"
            @change="e => changeOrderPrice(e.currentTarget.value)"
          />
          <label for="desc">{{ langTranslations.data[0].products.desc }}</label>
        </div>
        <div style="line-height: 1px">
          <input
            id="default"
            type="radio"
            value="default"
            name="orderPrice"
            @change="e => changeOrderPrice(e.currentTarget.value)"
          />
          <label for="default">{{
            langTranslations.data[0].products.noOrder
          }}</label>
        </div>
      </div>
    </div>
    <div class="row">
      <h5
        v-if="filteredProducts.length === 0"
        class="text-center"
        style="margin-top: 40px; width:100%"
      >
        {{ langTranslations.data[0].products.noResult }}
      </h5>
      <div
        v-else
        class="col-12 col-sm-6 col-md-4 col-lg-3"
        v-for="product in filteredProducts"
        :key="product.id"
      >
        <ProductItem :product="product" />
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import ProductItem from "@/components/products/ProductItem";
export default {
  components: { ProductItem },
  computed: mapGetters([
    "langTranslations",
    "categories",
    "minPrice",
    "maxPrice",
    "rangeValue",
    "filteredProducts",
    "activeBtn",
    "minPrice",
    "maxPrice",
    "rangeValue"
  ]),
  methods: mapActions([
    "chooseAll",
    "chooseCat",
    "setRangeValue",
    "changeOrderPrice",
    "showModal",
    "showDetails"
  ])
};
</script>

<style scoped>
.range {
  max-height: 45px;
  margin-bottom: 25px;
  text-align: center;
  position: relative;
  user-select: none;
  background-color: #484b4d;
  color: #9dd2d8;
  border: 2px outset #9dd2d8;
  border-radius: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (max-width: 550px) {
  .range {
    font-size: 0.8em;
    /* max-height: 100px; */
  }
  input[type="range"] {
    width: 35%;
  }
}
.range-value {
  display: block;
  color: #fc6e51;
  height: 25px;
  line-height: 25px;
}
@media (max-width: 350px) {
  .range-value {
    position: absolute;
    right: 10px;
    top: 25px;
  }
}
.range span {
  margin: 10px;
}
input[type="radio"] {
  margin: 5px;
  cursor: pointer;
}
label {
  cursor: pointer;
  display: flex !important;
  margin-top: 4px;
}

.title {
  margin-top: 25px;
}
@media (max-width: 768px) {
  .title {
    margin-top: 55px;
  }
}
@media (max-width: 400px) {
  .title {
    font-size: 1em;
    padding: 12px 0 0 0;
    margin-top: 25px;
  }
}
.tabs-container {
  padding: 15px;
  margin-bottom: 15px;
  overflow-x: auto;
}
.tabs-btns {
  width: 100%;
  display: flex;
}

button {
  border: 1px solid;
  padding: 3px 5px;
  width: 20%;
  cursor: pointer;
  background-color: white;
  outline: none !important;
}
.cat-buttons {
  width: 25%;
  /* border: 1px outset; */
}

button:hover {
  background-color: #a2d9df;
  color: white;
  transform: translateY(1px);
}
button.active {
  background-color: #ff7859;
  color: white;
  transform: translateY(5px);
}
/****  Input Range Styling  ***/
input[type="range"] {
  -webkit-appearance: none;
}
input[type="range"]:focus {
  outline: none;
  box-shadow: 0px 0px 5px #ff7859;
}
/***** Chrome ******/
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  background: white;
  border: 1px solid #ff7859;
}
input[type="range"]::-webkit-slider-thumb {
  height: 15px;
  width: 20px;
  border-radius: 3px;
  background: linear-gradient(#9dd2d8, #fff);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -4px;
  border: 1px solid #ff7859;
}
/***** Mozilla Fox  ******/
input[type="range"]::-moz-range-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  background: white;
  border: 1px solid #ff7859;
}
input[type="range"]::-moz-range-thumb {
  height: 15px;
  width: 20px;
  border-radius: 3px;
  background: linear-gradient(#9dd2d8, #fff);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -4px;
  border: 1px solid #ff7859;
}
/****** Internet Explorer ********/
input[type="range"]::-ms-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  background: white;
  border: 1px solid #ff7859;
}
input[type="range"]::-ms-fill-lower {
}
input[type="range"]::-ms-fill-upper {
}
input[type="range"]::-ms-thumb {
  height: 15px;
  width: 20px;
  border-radius: 3px;
  background: linear-gradient(#9dd2d8, #fff);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -4px;
  border: 1px solid #ff7859;
}
</style>
