<template>
  <section class="slider" v-if="sliderData.data">
    <div class="container">
      <div class="slider-outline">
        <ul
          class="thumbnails"
          :class="{
            'left-dir': direction === 'ltr',
            'right-dir': direction === 'rtl'
          }"
        >
          <li
            class="thumb"
            v-for="(slide, index) in sliderData.data"
            :key="slide.id"
            :style="{ backgroundImage: `url('${slide.url}.png')` }"
            :class="{ active: index === activeSlide }"
            @click="chooseSlide(slide)"
          ></li>
        </ul>
        <div class="slider-container">
          <div class="row" style="flex-direction: row-reverse; height: 100%">
            <div
              class="col-md-6 col-12"
              :class="{
                'main-image-rotate': direction === 'rtl'
              }"
            >
              <div
                id="mainImg"
                class="main-image"
                :style="{
                  backgroundImage: `url(${sliderData.data[activeSlide].url}.png)`
                }"
              >
                <span
                  id="next"
                  class="next fa fa-chevron-circle-right"
                  @click="nextSlide()"
                ></span>
                <span
                  class="prev fa fa-chevron-circle-left"
                  @click="prevSlide()"
                ></span>
              </div>
            </div>
            <div
              class="col-md-6 col-12"
              :class="{
                'text-align-left': direction === 'ltr',
                'text-align-right': direction === 'rtl'
              }"
            >
              <div
                class="heading"
                :class="{
                  'text-align-left': direction === 'ltr',
                  'text-align-right': direction === 'rtl'
                }"
              >
                <h2 class="title">
                  {{ sliderData.data[activeSlide].heading }}
                </h2>
                <h5 class="sub-title">
                  {{ sliderData.data[activeSlide].subTitle }}
                </h5>
                <p class="desc">{{ sliderData.data[activeSlide].desc }}</p>
                <div class="icon nav-item">
                  <router-link
                    class="nav-link"
                    :to="sliderData.data[activeSlide].btnHref"
                  >
                    <img
                      :src="`/${sliderData.data[activeSlide].btnImage}.png`"
                      alt="btn"
                    />
                    <span>{{ sliderData.data[activeSlide].btnTitle }}</span>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "slider",
  computed: mapGetters(["direction", "sliderData", "activeSlide"]),
  methods: mapActions(["chooseSlide", "nextSlide", "prevSlide"])
};
</script>

<style scoped>
.slider-outline {
  position: relative;
  width: 100%;
  min-height: 600px;
}
.thumbnails {
  position: absolute;
  list-style: none;
  width: 250px;
  height: 50px;
  display: flex;
  justify-content: center;
  padding: 0;
  bottom: 4%;
  z-index: 2;
}
.left-dir {
  left: 5%;
}
.right-dir {
  right: 5%;
}
@media (max-width: 768px) {
  .thumbnails {
    width: 100px;
    height: 250px;
    left: 0;
    top: 15vh;
    flex-direction: column;
  }
}
.thumb {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-size: cover;
  cursor: pointer;
  margin: 10px 3px;
}
@media (max-width: 500px) {
  .thumb {
    width: 30px;
    height: 30px;
  }
}
.thumb:hover {
  filter: brightness(1.2);
}
.thumb.active {
  filter: brightness(1.3);
  box-shadow: 1px 1px 15px;
}

.slider-container {
  width: 100%;
  height: 100%;
}
.heading {
  flex-direction: column;
  justify-content: center;
  height: 100%;
  display: flex;
  padding: 25px;
}
.btn {
  color: #484b4d;
  font-weight: bold;
  background-color: #fc6e51;
  padding: 7px 25px;
  align-self: flex-start;
}
.btn:hover {
  background-color: transparent;
  border: 1px solid currentColor;
}
@media (max-width: 768px) {
  .heading {
    height: 50vh;
    margin-top: 50px;
  }
}
@media (max-width: 450px) {
  .heading {
    padding: 0;
    height: 65vh;
    margin-top: 50px;
  }
}

.title {
  font-family: fantasy;
}
.sub-title {
  padding-top: 15px;
  font-family: fantasy;
}
.desc {
  margin-top: 15px;
  font-size: 1em;
}
.main-image-rotate {
  transform: rotateY(180deg);
}
.main-image {
  height: 600px;
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  position: relative;
}
@media (max-width: 500px) {
  .main-image {
    height: calc(100vh - 100px);
    background-position: center;
  }
}
.fadeEffect {
  animation: fade 1 1s forwards;
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.next {
  position: absolute;
  display: block;
  font-size: 28px;
  border-radius: 50%;
  top: 50%;
  right: -5%;
  opacity: 0.4;
  cursor: pointer;
}
.next:hover {
  color: #ccc;
}

.prev {
  position: absolute;
  display: block;
  font-size: 28px;
  border-radius: 50%;
  top: 50%;
  left: -115%;
  opacity: 0.4;
  cursor: pointer;
}
.prev:hover {
  color: #ccc;
}
@media (max-width: 768px) {
  .prev {
    top: 56%;
    left: 95%;
  }
}
.text-align-left {
  text-align: left !important;
}
.text-align-right {
  text-align: right !important;
}
.icon {
  margin-right: 2px !important;
}
.icon:hover {
  filter: brightness(1.4);
}
.icon img {
  height: 50px;
  margin: 0 10px;
  animation: flasher 10s infinite linear;
}
@keyframes flasher {
  0% {
    filter: brightness(1);
  }
  65% {
    filter: brightness(1);
    transform: scale(1);
  }
  70% {
    filter: brightness(1.2);
    transform: scale(1.1);
  }
  75% {
    filter: brightness(1);
    transform: scale(1);
  }
  82% {
    filter: brightness(1.4);
    transform: scale(1.1);
  }
  90% {
    filter: brightness(1);
    transform: scale(1);
  }
}
</style>
