<template>
  <div class="clear-basket-container" v-if="showConfirmMsg">
    <div class="confirm-message text-center">
      <p>{{ langTranslations.data[0].confirmClr.title }}</p>
      <div>
        <button class="btn btn-danger" @click="clearBasket()">
          {{ langTranslations.data[0].confirmClr.clear }}
        </button>
        <button class="btn btn-info" @click="hideConfirm()">
          {{ langTranslations.data[0].confirmClr.cancel }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "ClearBasketModal",
  computed: mapGetters(["langTranslations", "showConfirmMsg"]),
  methods: mapActions(["hideConfirm", "clearBasket"])
};
</script>

<style scoped>
.clear-basket-container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,.5);
  z-index: 10000;
}
.confirm-message {
  width: 50%;
  min-height: 150px;
  padding: 25px;
  background-color: #eee;
  font-weight: bold;
  border-radius: 5px;
  font-size: 1.2em;
  opacity: 0;
  animation: fade 1 0.5s linear;
  animation-fill-mode: forwards;
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
button {
  margin: 25px;
}
@media(max-width: 500px) {
  .confirm-message {
  width: 75%;
  }
}
</style>
