<template>
  <div class="detail-slider-container">
    <div class="text-center">
      <img
        :src="images[activeImage]"
        alt="main-slide"
        class="main-detial-img img-thumbnail"
      />
    </div>
    <div class="details-thumbnails text-center">
      <span
        class="next-slide"
        @click="direction === 'ltr' ? handleNext() : handlePrev()"
      >
        {{ direction === "ltr" ? gt : lt }}
      </span>
      <span
        class="prev-slide"
        @click="direction === 'ltr' ? handlePrev() : handleNext()"
      >
        {{ direction === "ltr" ? lt : gt }}
      </span>
      <img
        v-for="(img, index) in images"
        :key="img + Math.random()"
        :class="
          activeImage === index
            ? 'detailed-images active-detail-image'
            : 'detailed-images'
        "
        :src="`/${img}`"
        alt="product"
        @click="handleClick(index)"
      />
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "DetailSlider",
  props: ["images"],
  computed: mapGetters(["direction"]),
  data() {
    return {
      activeImage: 0,
      gt: ">>>",
      lt: "<<<"
    };
  },
  methods: {
    handleNext: function() {
      const index = this.activeImage + 1;
      const newIndex = index !== this.images.length ? this.activeImage + 1 : 0;
      this.activeImage = newIndex;
    },
    handlePrev: function() {
      const index = this.activeImage - 1;
      const newIndex =
        index !== -1 ? this.activeImage - 1 : this.images.length - 1;
      this.activeImage = newIndex;
    },
    handleClick: function(index) {
      this.activeImage = index;
    }
  }
};
</script>
<style scoped>
.detail-slider-container {
  width: 100%;
  height: 100%;
  position: relative;
}
.main-detial-img {
  width: 50%;
  height: 300px;
}
.details-thumbnails {
  display: flex;
  justify-content: center;
  position: absolute;
  top: 80%;
  width: 100%;
  box-shadow: inset 1px 1px 15px 8px #9dd2d8;
  flex-wrap: nowrap;
  overflow-x: auto;
  background-color: #ffffff20;
  border-radius: 5px;
}
.detailed-images {
  width: 35px;
  height: 45px;
  margin: 2px 5px;
  cursor: pointer;
  transform: scale(0.9);
  border-radius: 5px;
}
span {
  display: block;
  width: 25px;
  height: 25px;
  position: absolute;
  top: calc(50% - 15px);
  cursor: pointer;
  color: #fc6e51;
  font-size: 1.2em;
}
.next-slide {
  right: 15px;
}
.prev-slide {
  left: 15px;
}
.active-detail-image {
  border: 3px solid #fff;
  transform: scale(1) !important;
}

@media (max-width: 992px) {
  .main-detial-img {
    width: 75%;
  }
}
@media (max-width: 576px) {
  .main-detial-img {
    width: 75%;
  }
}
</style>
