<template>
  <div class="slide-slider">
    <div class="scroll-view">
      <button
        class="forward fa btn btn-info-outline"
        :class="
          direction === 'rtl'
            ? 'fa-chevron-left forward-dir-rtl'
            : 'fa-chevron-right'
        "
        @click="toForward()"
        :disabled="clickCounter <= 0 ? 'disabled' : null"
      />
      <button
        class="backward fa btn btn-info-outline"
        :class="
          direction === 'rtl'
            ? 'fa-chevron-right backward-dir-rtl'
            : 'fa-chevron-left'
        "
        @click="toBackward()"
        :disabled="clickCounter >= lastCount ? 'disabled' : null"
      />
      <div
        class="h-scroll"
        :style="
          direction === 'ltr'
            ? { left: this.leftValue + '%' }
            : { right: this.leftValue + '%' }
        "
      >
        <div
          class="col-12 col-sm-6 col-lg-4 item"
          v-for="product in data"
          :key="product.id"
        >
          <ProductItem :product="product" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ProductItem from "@/components/products/ProductItem";
import { mapGetters } from "vuex";
export default {
  name: "SlideSlider",
  components: { ProductItem },
  props: ["data", "length", "itemsCount"],
  computed: mapGetters(["direction"]),
  data() {
    return {
      lastCount: "",
      leftValue: 0,
      clickCounter: 0
    };
  },
  created: function() {
    if (window.innerWidth >= 992) {
      this.lastCount = this.length / this.itemsCount - 1;
    } else if (window.innerWidth < 992 && window.innerWidth > 575) {
      this.lastCount = this.length / (this.itemsCount - 1) - 1;
    } else if (window.innerWidth <= 575) {
      this.lastCount = this.length - 1;
    }
  },
  methods: {
    toForward: function() {
      if (this.clickCounter > 0) {
        this.leftValue = this.leftValue + 100;
        this.clickCounter = this.clickCounter - 1;
      }
    },
    toBackward: function() {
      if (this.clickCounter < this.lastCount) {
        this.leftValue = this.leftValue - 100;
        this.clickCounter = this.clickCounter + 1;
      }
    }
  }
};
</script>

<style scoped>
.scroll-view {
  position: relative;
  overflow: hidden;
}
.forward,
.backward {
  position: absolute;
  top: 150px;
  z-index: 10;
  height: 100px;
  box-shadow: none !important;
}
.forward:not([disabled]):hover,
.backward:not([disabled]):hover {
  box-shadow: 1px 1px 4px #777 !important;
}
.forward {
  right: 5px;
}
.backward {
  left: 5px;
}
.forward-dir-rtl {
  left: 5px;
  right: auto;
}
.backward-dir-rtl {
  right: 5px;
  left: auto;
}
.h-scroll {
  position: relative;
  display: flex;
  transition: all 1.7s ease-in-out;
}
</style>
