const state = {
  isLoading: true,
  taxValue: 0,
  contact: false,
};
const getters = {
  isLoading: state => state.isLoading,
  taxValue: state => state.taxValue,
  contact: state => state.contact
};
const actions = {
  showContact: ({ commit }) => commit("contact", true),
  hideContact: ({ commit }) => commit("contact", false)
};
const mutations = {
  stopLoading: state => (state.isLoading = false),
  contact: (state, newValue) => (state.contact = newValue)
};

export default {
  state,
  getters,
  actions,
  mutations
};
