<template>
  <div class="contact" :class="contact ? 'show' : 'hide'">
    <div class="envelope">
      <form
        action="mailto: Rawadj.khair@gmail.com"
        method="POST"
        @submit="e => e.stopPropagation()"
      >
        <textarea
          :placeholder="langTranslations.data[0].contact.message"
          class="input-control"
        />
        <input
          type="text"
          :placeholder="langTranslations.data[0].contact.name"
          class="input-control name"
          required
        />
        <input
          type="email"
          :placeholder="langTranslations.data[0].contact.email"
          class="input-control email"
          required
        />
        <input
          type="submit"
          :value="langTranslations.data[0].contact.send"
          class="stamp"
          :style="
            direction === 'ltr'
              ? { right: '25%' }
              : { left: '25%', right: 'auto' }
          "
        />
      </form>
      <button
        class="fa fa-close btn btn-danger special-position"
        :style="direction === 'ltr' ? { right: 0 } : { left: 0, right: 'auto' }"
        @click="hideContact"
      />
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Contact",
  computed: mapGetters(["direction", "langTranslations", "contact"]),
  methods: mapActions(["hideContact"])
};
</script>
<style scoped>
.contact {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: -100%;
  left: 0;
  background-color: #dce6ef95;
  justify-content: center;
  align-items: center;
  opacity: 0;
  display: none;
  z-index: -1;
  transition: all 1s;
}
.hide {
  animation: slideUp 0.9s 1 linear;
  animation-fill-mode: forwards;
}
.show {
  opacity: 1;
  animation: slideDown 0.9s 1 linear;
  animation-fill-mode: forwards;
  display: flex;
  z-index: 10000;
}
@keyframes slideUp {
  0% {
    top: 0;
  }
  100% {
    top: -100%;
  }
}
@keyframes slideDown {
  0% {
    top: -100%;
  }
  100% {
    top: 0;
  }
}

.envelope {
  background-image: url("/images/icons/envelope.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  height: 450px;
  width: 50%;
  position: relative;
  padding: 0;
}
@media (max-width: 992px) {
  .envelope {
    width: 80%;
  }
}
.special-position {
  position: absolute;
  top: 0;
  border-radius: 50%;
}
form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
}
.input-control {
  margin: 5px;
  padding: 10px;
  outline: none;
  border: none;
  position: absolute;
}
textarea {
  height: 25%;
  width: 50%;
  resize: none;
  top: 20%;
  left: 25%;
}
.name {
  bottom: 65px;
  left: 35%;
  width: 30%;
}
.email {
  bottom: 35px;
  left: 35%;
  width: 30%;
}
.stamp {
  display: block;
  width: 75px;
  height: 75px;
  position: absolute;
  bottom: -25px;
  outline: none;
  border: none;
  background-image: url("/images/icons/stamp.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  border-radius: 50%;
  border-radius: 50%;
  color: white;
}
.stamp:hover {
  color: #f6232f;
}
@media (max-width: 500px) {
  .name,
  .email {
    font-size: 0.6rem;
  }
}
</style>
