<template>
  <section class="login">
    <div :class="direction === 'ltr' ? 'log-in' : 'log-in text-right'">
      <form style="position: relative; margin-top: 60px">
        <div class="row">
          <div class="col-md-4"></div>
          <div class="col-md-4">
            <div class="form-group">
              <label>{{ langTranslations.data[0].login.email }}</label>
              <input
                type="email"
                class="form-control"
                :placeholder="langTranslations.data[0].login.emailPH"
              />
            </div>
            <div class="form-group">
              <label>{{ langTranslations.data[0].login.password }}</label>
              <input
                type="password"
                class="form-control"
                :placeholder="langTranslations.data[0].login.passwordPH"
              />
            </div>
          </div>
          <div class="col-md-1">
            <button
              type="submit"
              class="btn btn-info"
              style="height: 100%; white-space: normal"
            >
              {{ langTranslations.data[0].login.loginBtn }}
            </button>
          </div>
        </div>
      </form>
    </div>
    <div class="text-center col-md-4 m-auto">
      <p class="mt-4">{{ langTranslations.data[0].login.noAccount }}</p>
      <router-link to="/register" class="btn btn-info btn-block">
        {{ langTranslations.data[0].login.logupBtn }}
      </router-link>
    </div>
  </section>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Login",
  computed: mapGetters(["direction", "langTranslations"])
};
