<template>
  <div
    class="table-row"
    :style="
      item.count === 0
        ? { backgroundColor: 'oldlace' }
        : { backgroundColor: 'white' }
    "
  >
    <div class="table-cell title">
      <h6 :class="direction === 'ltr' ? 'text-left' : 'text-right'">
        {{ item.title }}
      </h6>
    </div>
    <div class="table-cell">
      <img :src="item.images[0]" alt="product" class="product-image" />
    </div>
    <div class="table-cell">
      <h6>{{ item.price }}</h6>
    </div>
    <div class="table-cell withBtn">
      <button
        class="btn btn-sm btn-outline-danger"
        @click="decreaseQuantity(item.id)"
      >
        <i class="fa fa-minus" />
      </button>
      <h6>{{ item.count }}</h6>
      <button
        class="btn btn-sm btn-outline-info"
        @click="increaseQuantity(item.id)"
      >
        <i class="fa fa-plus" />
      </button>
    </div>
    <div class="table-cell remove-item">
      <span class="fa fa-trash fa-x" @click="removeItem(item.id)" />
    </div>
    <div class="table-cell">
      <h5>{{ item.total.toFixed(2) }}</h5>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "BasketTableRow",
  props: ["item"],
  computed: mapGetters(["direction"]),
  methods: mapActions(["increaseQuantity", "decreaseQuantity", "removeItem"])
};
</script>

<style scoped>
.table-row {
  display: table-row;
  position: relative;
}
.table-cell {
  margin: auto;
  max-width: 15vw;
  display: table-cell;
  padding: 10px;
  text-align: center;
}
.table-cell:first-child {
  max-width: 25vw;
}
.withBtn {
  display: flex;
  justify-content: space-around;
}
.withBtn .fa {
  cursor: pointer;
}
@media (max-width: 768px) {
  .title {
    padding: 0;
  }
  .table-cell h6 {
    font-size: 0.9em !important;
    display: flex;
    justify-content: center;
    align-self: center;
    margin: 0;
  }
  .table-cell h5 {
    font-size: 1em !important;
  }
}
.product-image {
  width: 40px;
  height: 50px;
}
.remove-item .fa {
  cursor: pointer;
}
.remove-item .fa:hover {
  color: red;
}
button {
  border: none !important;
}
</style>
