<template>
  <section class="basket">
    <div v-if="basket.length !== 0">
      <div class="basket-container">
        <h3
          class="title"
          :class="direction === 'ltr' ? 'text-left' : 'text-right'"
        >
          {{ langTranslations.data[0].basket.title }}
        </h3>
        <div class="basket-table">
          <div class="table-row">
            <div class="table-cell">
              <h4
                class="cell-data"
                :class="direction === 'ltr' ? 'text-left' : 'text-right'"
              >
                {{ langTranslations.data[0].basket.product }}
              </h4>
            </div>
            <div class="table-cell">
              <h4 class="cell-data">
                {{ langTranslations.data[0].basket.image }}
              </h4>
            </div>
            <div class="table-cell">
              <h4 class="cell-data">
                {{ langTranslations.data[0].basket.price }}
              </h4>
            </div>
            <div class="table-cell">
              <h4 class="cell-data">
                {{ langTranslations.data[0].basket.quantity }}
              </h4>
            </div>
            <div class="table-cell">
              <h4 class="cell-data">
                {{ langTranslations.data[0].basket.remove }}
              </h4>
            </div>
            <div class="table-cell">
              <h4 class="cell-data">
                {{ langTranslations.data[0].basket.total }}
              </h4>
            </div>
          </div>
          <BasketTableRow v-for="item in basket" :key="item.id" :item="item" />
        </div>
      </div>
      <div
        class="clear-basket"
        :style="
          direction === 'ltr'
            ? { right: '10%' }
            : { left: '10%', right: 'auto' }
        "
      >
        <button class="btn btn-outline-danger" @click="showConfirm">
          {{ langTranslations.data[0].basket.clrBasket }}
        </button>
      </div>
      <div class="sum">
        <h4 class="sum-data">
          {{ langTranslations.data[0].basket.subTotal }}:
          {{ Math.floor(sumOfTotal) }}
        </h4>
        <h4 class="sum-data">
          {{ langTranslations.data[0].basket.tax }}: {{ taxValue }} %
        </h4>
        <hr />
        <h3 style="color: #FC6E51">
          {{ langTranslations.data[0].basket.final }}:
          {{ Math.floor(sumOfTotal + (sumOfTotal * taxValue) / 100) }}
        </h3>
      </div>
    </div>
    <div v-if="basket.length === 0" class="basket-container">
      <p class="text-capitalize text-center">
        {{ langTranslations.data[0].basket.emptyBasket }}
      </p>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import BasketTableRow from "@/components/basket/BasketTableRow";
export default {
  name: "Basket",
  components: { BasketTableRow },
  computed: mapGetters([
    "langTranslations",
    "direction",
    "basket",
    "sumOfTotal",
    "taxValue"
  ]),
  methods: mapActions(["showConfirm"])
};
</script>
<style scoped>
.basket {
  margin-bottom: 150px;
}
.basket-container {
  padding-top: 20px;
}
@media (max-width: 768px) {
  .basket-container {
    padding-top: 70px;
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 1.25em;
  }
}
.basket-table {
  display: table;
  width: 100%;
  margin: 25px 0;
}
.table-row {
  display: table-row;
  position: relative;
}
.table-cell {
  margin: auto;
  max-width: 15vw;
  display: table-cell;
  padding: 10px;
  text-align: center;
}
.table-cell:first-child {
  max-width: 25vw;
}
.cell-data {
  font-size: 1.2em;
}
@media (max-width: 768px) {
  .table-cell {
    font-size: 1em;
  }
  .cell-data {
    font-size: 0.8em;
  }
}
.clear-basket {
  position: absolute;
}
.sum {
  background-color: #9dd2d8;
  position: fixed;
  opacity: 0.9;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: flex-end;
  width: 100%;
  justify-content: space-around;
  border-radius: 3px;
  margin-bottom: 0;
}
@media (max-width: 768px) {
  .sum-data {
    font-size: 1em;
  }
  .sum h3 {
    font-size: 1.02em;
  }
}
</style>
