import axios from "axios";

const state = {
  direction: "ltr",
  activeLang: "en",
  langTranslations: []
};
const getters = {
  direction: state => state.direction,
  activeLang: state => state.activeLang,
  langTranslations: state => state.langTranslations
};
const actions = {
  changeLanguage: ({ commit, dispatch }, lang) => {
    commit("setLanguage", lang);
    dispatch("fetchData");
  },
  getLangTranslations: async ({ commit, state }) => {
    let langTranslations;
    if (state.activeLang === "en") {
      langTranslations = await axios.get("json/langTranslations_En.json");
    }
    if (state.activeLang === "ar") {
      langTranslations = await axios.get("json/langTranslations_Ar.json");
    }
    commit("setLangTranslations", langTranslations);
  }
};
const mutations = {
  setLanguage: (state, lang) => {
    if (lang === "en") {
      state.activeLang = "en";
      state.direction = "ltr";
    }
    if (lang === "ar") {
      state.activeLang = "ar";
      state.direction = "rtl";
    }
  },
  setLangTranslations: (state, langTranslations) => {
    state.langTranslations = langTranslations;
    const html = document.getElementsByTagName("html")[0];
    html.lang = state.activeLang;
    html.style.direction = state.direction;
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
