import axios from "axios";

const state = {
  products: [],
  filteredProducts: [],
  featuredProducts: [],
  mostVisitedProducts: [],
  details: [],
  basket: [],
  activeBtn: "all",
  categories: [],
  rangeValue: null,
  minPrice: null,
  maxPrice: null,
  order: "default",
  modalOpen: false,
  showConfirmMsg: false
};
const getters = {
  products: state => state.products,
  filteredProducts: state => state.filteredProducts,
  featuredProducts: state => state.featuredProducts,
  mostVisitedProducts: state => state.mostVisitedProducts,
  details: state => state.details,
  basket: state => state.basket,
  sumOfTotal: state => {
    let total = 0;
    state.basket.forEach(item => (total += item.total));
    return total;
  },
  activeBtn: state => state.activeBtn,
  categories: state => state.categories,
  rangeValue: state => state.rangeValue,
  minPrice: state => state.minPrice,
  maxPrice: state => state.maxPrice,
  order: state => state.order,
  modalOpen: state => state.modalOpen,
  showConfirmMsg: state => state.showConfirmMsg
};
const actions = {
  fetchData: async ({ commit, dispatch, rootState }) => {
    dispatch("getLangTranslations");
    let products = null;
    if (rootState.languages.activeLang === "en") {
      products = await axios.get("json/productsData_En.json");
      // result = productsDataEn
    }
    if (rootState.languages.activeLang === "ar") {
      products = await axios.get("json/productsData_Ar.json");
      // result = productsDataAr;
    }
    const prices = [],
      categories = new Set();
    products.data.forEach(item => {
      prices.push(item.price);
      categories.add(item.category);
    });
    const minPrice = Math.floor(Math.min(...prices));
    const maxPrice = Math.ceil(Math.max(...prices) / 10) * 10;
    commit("setData", { products, minPrice, maxPrice, categories });
    dispatch("startSlider");
    setTimeout(function () {
      commit("stopLoading");
    }, 1500);
  },
  addToBasket: ({ commit }, id) => {
    const reqItem = state.products.find(item => item.id === id);
    reqItem.inCart = true;
    reqItem.count += 1;
    reqItem.total += reqItem.price;
    commit("addToBasket", reqItem);
  },
  showDetails: ({ commit }, id) => {
    const reqItem = state.products.find(item => item.id === id);
    commit("setItemInDetails", reqItem);
  },
  showModal: ({ commit, state, dispatch }, id) => {
    const reqItem = state.products.find(item => item.id === id);
    dispatch("addToBasket", id);
    commit("setItemInDetails", reqItem);
    commit("showModal");
  },
  closeModal: ({ commit }) => {
    commit("closeModal");
  },
  increaseQuantity: ({ commit, state }, id) => {
    const reqItem = state.basket.find(item => item.id === id);
    reqItem.count += 1;
    reqItem.total = reqItem.count * reqItem.price;
    const newBasket = [...state.basket];
    commit("setNewBasket", newBasket);
  },
  decreaseQuantity: ({ commit, state }, id) => {
    const reqItem = state.basket.find(item => item.id === id);
    if (reqItem.count > 0) {
      reqItem.count -= 1;
      reqItem.total = reqItem.count * reqItem.price;
      const newBasket = [...state.basket];
      commit("setNewBasket", newBasket);
    } else {
      return false;
    }
  },
  removeItem: ({ commit, state }, id) => {
    const reqItem = state.basket.find(item => item.id === id);
    reqItem.inCart = false;
    reqItem.count = 0;
    reqItem.total = 0;
    const newBasket = [...state.basket];
    const index = newBasket.indexOf(reqItem);
    if (index !== -1) {
      newBasket.splice(index, 1);
      commit("setNewBasket", newBasket);
    }
  },
  clearBasket: ({ state, commit, dispatch }) => {
    state.basket.forEach(item => dispatch("removeItem", item.id));
    const newBasket = [];
    commit("setNewBasket", newBasket);
    commit("hideConfirmMsg");
    setTimeout(function () {
      const productsTab = document.getElementById("products");
      productsTab.click();
    }, 1000);
  },
  showConfirm: ({ commit }) => {
    commit("showConfirmMsg");
  },
  hideConfirm: ({ commit }) => {
    commit("hideConfirmMsg");
  },
  chooseCat: ({ commit, state, dispatch }, cat) => {
    commit("setActiveBtn", cat);
    const filteredData = state.products.filter(
      product => product.category === cat && product.price <= state.rangeValue
    );
    commit("setFilteredProducts", filteredData);
    dispatch("changeOrderPrice", state.order);
    window.scrollTo(0, 0);
  },
  chooseAll: ({ commit, state, dispatch }) => {
    const filteredData = state.products.filter(
      product => product.price <= state.rangeValue
    );
    commit("setActiveBtn", "all");
    commit("setFilteredProducts", filteredData);
    dispatch("changeOrderPrice", state.order);
  },
  setRangeValue: ({ commit, state }, value) => {
    commit("setRangeValue", value);
    let filteredData = state.products.filter(v => v.price <= state.rangeValue);
    if (state.activeBtn !== "all") {
      filteredData = filteredData.filter(v => v.category === state.activeBtn);
    }
    let newFilteredData;
    if (state.order === "acc") {
      newFilteredData = filteredData.sort(function (a, b) {
        return a.price - b.price;
      });
    } else if (state.order === "desc") {
      newFilteredData = filteredData.sort(function (a, b) {
        return b.price - a.price;
      });
    } else if (state.order === "default") {
      newFilteredData = filteredData.sort(function (a, b) {
        return a.id - b.id;
      });
    }
    commit("setFilteredProducts", newFilteredData);
  },
  changeOrderPrice: ({ commit, state }, value) => {
    let filteredData;
    if (value === "acc") {
      filteredData = state.filteredProducts.sort(function (a, b) {
        return a.price - b.price;
      });
    } else if (value === "desc") {
      filteredData = state.filteredProducts.sort(function (a, b) {
        return b.price - a.price;
      });
    } else if (value === "default") {
      filteredData = state.filteredProducts.sort(function (a, b) {
        return a.id - b.id;
      });
    }
    commit("setFilteredProducts", filteredData);
    commit("setOrder", value);
  },
  search: ({ state, commit, dispatch }, value) => {
    if (value !== "") {
      const searchWord = new RegExp(value, "i");
      const filteredDataBySearch = state.products.filter(item =>
        item.title.match(searchWord)
      );
      if (state.activeBtn !== "all") {
        const filteredData = filteredDataBySearch.filter(
          product =>
            product.category === state.activeBtn &&
            product.price <= state.rangeValue
        );
        commit("setFilteredProducts", filteredData);
        dispatch("changeOrderPrice", state.order);
      }
      if (state.activeBtn === "all") {
        const filteredData = filteredDataBySearch.filter(
          product => product.price <= state.rangeValue
        );
        commit("setFilteredProducts", filteredData);
        dispatch("changeOrderPrice", state.order);
      }
    } else {
      if (state.activeBtn !== "all") {
        const filteredData = state.products.filter(
          product =>
            product.category === state.activeBtn &&
            product.price <= state.rangeValue
        );
        commit("setFilteredProducts", filteredData);
        dispatch("changeOrderPrice", state.order);
      }
      if (state.activeBtn === "all") {
        const filteredData = state.products.filter(
          product => product.price <= state.rangeValue
        );
        commit("setFilteredProducts", filteredData);
        dispatch("changeOrderPrice", state.order);
      }
    }
  }
};
const mutations = {
  setData: (state, payload) => {
    (state.products = payload.products.data),
      (state.filteredProducts = payload.products.data),
      (state.minPrice = payload.minPrice),
      (state.maxPrice = payload.maxPrice),
      (state.rangeValue = payload.maxPrice),
      (state.mostVisitedProducts = payload.products.data.filter(
        product => product.mostVisited === true
      )),
      (state.featuredProducts = payload.products.data.filter(
        product => product.featured === true
      )),
      (state.categories = payload.categories);
  },
  addToBasket: (state, newItem) => state.basket.push(newItem),
  setNewBasket: (state, newBasket) => (state.basket = newBasket),
  setItemInDetails: (state, reqItem) => (state.details = reqItem),
  showModal: state => (state.modalOpen = true),
  closeModal: state => (state.modalOpen = false),
  showConfirmMsg: state => (state.showConfirmMsg = true),
  hideConfirmMsg: state => (state.showConfirmMsg = false),
  setActiveBtn: (state, btn) => (state.activeBtn = btn),
  setFilteredProducts: (state, newProducts) =>
    (state.filteredProducts = newProducts),
  setRangeValue: (state, newValue) => (state.rangeValue = newValue),
  setOrder: (state, value) => (state.order = value)
};

export default {
  state,
  getters,
  actions,
  mutations
};
