import Vue from "vue";
import VueRouter from "vue-router";

import Home from "../views/Home.vue";
import Products from "../views/Products.vue";
import Basket from "../views/Basket.vue";
import Details from "../views/Details.vue";
import Auth from "../views/Auth.vue";
import LogUp from "../components/auth/LogUp.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/products",
    name: "Products",
    component: Products
  },
  {
    path: "/basket",
    name: "basket",
    component: Basket
  },
  {
    path: "/details",
    name: "Details",
    component: Details
  },
  {
    path: "/login",
    name: "login",
    component: Auth
  },
  {
    path: "/register",
    name: "register",
    component: LogUp
  }
];

const router = new VueRouter({
  // mode: "history",
  routes
});

export default router;
