<template>
  <a id="scrollUpBtn" @click="scrollToTop"><i class="fa fa-chevron-up"></i></a>
</template>
<script>
export default {
  name: "ScrollToTop",
  methods: {
    scrollToTop() {
      window.scrollTo(0, 0);
    }
  }
};
</script>
<style scoped>
a {
  position: fixed;
  z-index: 100000;
  display: block;
  bottom: 10px;
  right: 10px;
  padding: 5px 10px;
  background: #484b4d;
  border-radius: 2px;
  animation: bounce 2s ease infinite;
  opacity: 0;
  transition: opacity 1.5s;
}
i {
  color: #fff;
}
a:hover {
  color: white;
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
</style>
