<template>
  <div class="product-item" @click="showDetails(product.id)">
    <div class="card">
      <router-link to="/details">
        <img
          class="card-img-top"
          :src="product.images[0]"
          alt="Product Image"
          :title="
            direction === 'ltr'
              ? 'for more info ... Click On'
              : 'لمعرفة المواصفات .. انقر على الصورة'
          "
        />
        <div class="product-body card-body">
          <h5 class="product-title card-title text-capitalize">
            {{ product.title }}
          </h5>
          <p class="product-price card-text">{{ product.price }} $</p>
          <span class="old-price card-text" v-if="product.specialOffer"
            >{{ product.oldPrice }} $</span
          >
        </div>
      </router-link>
      <button
        href="#"
        class="btn btn-info buy-button"
        @click="showModal(product.id)"
        v-if="product.inCart === false"
      >
        {{ langTranslations.data[0].productItem.addTo }}
        <i class="fa fa-cart-plus" style="margin: 5px" />
      </button>
      <button
        href="#"
        class="btn btn-danger buy-button fixed"
        disabled
        v-if="product.inCart"
      >
        {{ langTranslations.data[0].productItem.inCart }}
      </button>
      <span :class="product.specialOffer ? 'special-offer' : 'hide'">{{
        product.specialOffer ? "Special Offer" : null
      }}</span>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "ProductItem",
  props: ["product"],
  methods: mapActions(["showModal", "showDetails"]),
  computed: mapGetters(["langTranslations", "direction"])
};
</script>

<style scoped>
.card {
  cursor: pointer;
  height: 320px;
  margin: 20px 5px;
  padding: 0;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: 0.4s linear;
  border-radius: 6px;
}
.card:hover {
  filter: brightness(1.11) drop-shadow(2px 2px 8px black);
}
.card-img-top {
  margin: 15px 0 0 0;
  padding: 2%;
  width: 170px;
  height: 200px;
  align-self: center;
  transition: 0.4s linear;
}
.card:hover .card-img-top {
  transform: scale(1.05);
}
.card a {
  text-decoration: none;
}
.product-image {
  margin: 15px 0 0 0;
  padding: 2%;
  width: 170px;
  height: 200px;
  align-self: center;
  transition: 0.4s linear;
}
.product-body {
  padding: 2px 5px;
  position: relative;
  height: 100px;
}
.product-title {
  color: #222;
  font-weight: bold;
  font-size: 16px;
  margin: 5px 0 0 0;
}
.product-price,
.old-price {
  margin: 0;
  display: inline-block;
  padding: 0;
  color: red;
  font-weight: bold;
  font-size: 18px;
  position: absolute;
  right: 8px;
  bottom: 10px;
}
.old-price {
  color: #777;
  bottom: 35px;
  text-decoration: line-through;
}
.buy-button {
  position: absolute;
  right: 0;
  top: -40px;
  transform: translate(105%, 0);
  transition: 0.35s linear;
  z-index: 9;
}
.fixed {
  transform: none;
  top: 2px;
  right: 2px;
}
.buy-button:not(.btn-danger):hover {
  background-color: #007bff;
  border-color: #007bff;
}
.card:hover .buy-button {
  transform: translate(0, 40px);
  transition: 0.4s linear;
}
.card:hover .buy-button.fixed {
  transform: none;
  top: 4px;
  right: 4px;
}
.special-offer {
  display: block;
  width: 137px;
  height: 35px;
  background-color: #ff7859;
  color: #fff;
  position: absolute;
  top: 13px;
  left: -38px;
  transform: rotateZ(-45deg);
  font-size: 0.7em;
  line-height: 2.5;
}
.hide {
  display: none;
}
</style>
