<template>
  <section class="auth">
    <div :class="direction === 'ltr' ? 'log-up' : 'log-up text-right'">
      <form style="position: relative; margin-top: 60px">
        <div class="row">
          <div class="col-lg-1"></div>
          <div class="col-md-5">
            <div class="form-group">
              <label>{{ langTranslations.data[0].logup.fullname }}</label>
              <input
                type="text"
                class="form-control"
                :placeholder="langTranslations.data[0].logup.fullnamePH"
              />
            </div>
            <div class="form-group">
              <label>{{ langTranslations.data[0].logup.email }}</label>
              <input
                type="email"
                class="form-control"
                :placeholder="langTranslations.data[0].logup.emailPH"
              />
            </div>
            <div class="form-group">
              <label>{{ langTranslations.data[0].logup.mobile }}</label>
              <input
                type="text"
                class="form-control"
                :placeholder="langTranslations.data[0].logup.mobilePH"
                maxLength="10"
              />
            </div>
          </div>
          <div class="col-md-5">
            <div class="form-group">
              <label>{{ langTranslations.data[0].logup.address }}</label>
              <textarea
                class="form-control"
                :placeholder="langTranslations.data[0].logup.addressPH"
                maxLength="100"
                style=" resize:none; height: 125px"
              />
            </div>
            <div class="form-group">
              <label>{{ langTranslations.data[0].logup.password }}</label>
              <input
                type="password"
                class="form-control"
                :placeholder="langTranslations.data[0].logup.passwordPH"
              />
            </div>
            <div class="form-group">
              <label>{{ langTranslations.data[0].logup.password2 }}</label>
              <input
                type="password"
                class="form-control"
                :placeholder="langTranslations.data[0].logup.password2PH"
              />
            </div>
          </div>
          <div class="col-lg-1 col-md-2">
            <button
              type="submit"
              class="btn btn-info btn-block"
              style=" height: 100%; white-space: normal"
            >
              {{ langTranslations.data[0].logup.logupBtn }}
            </button>
          </div>
          <h6
            class="flasher-note"
            id="flasherNote"
            :class="direction === 'rtl' ? 'adjustBtn' : ''"
          >
            {{ langTranslations.data[0].logup.note }}
            <i class="fa fa-close" @click="closeNote()"></i>
          </h6>
        </div>
      </form>
    </div>
    <hr />
    <About />
  </section>
</template>

<script>
import { mapGetters } from "vuex";
import About from "@/components/About";

export default {
  name: "LogUp",
  components: { About },
  computed: mapGetters(["direction", "langTranslations"]),
  methods: {
    closeNote: () => {
      let note = document.getElementById("flasherNote");
      const classString = note.className;
      const newClass = classString.concat(" hide");
      classString = newClass;
    }
  }
};
</script>
<style scoped>
.auth {
  padding: 0 2px;
}
.flasher-note {
  padding: 10px;
  border-radius: 2px;
  position: absolute;
  bottom: 5%;
  right: 65%;
  background-color: darkorange;
  color: #fff;
  animation: flashNote 1.4s infinite;
  animation-direction: alternate;
}
@keyframes flashNote {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.7;
  }
}
.flasher-note.hide {
  display: none;
}
.flasher-note .fa {
  margin: 5px;
  cursor: pointer;
}
.flasher-note .fa:hover {
  color: red;
}
@media (max-width: 768px) {
  .flasher-note {
    max-width: 100px;
    top: 2%;
    left: 65%;
    right: auto;
    height: 200px;
    padding-top: 25px;
    line-height: 1.58;
  }
  .falser-note .fa {
    position: absolute;
    top: 0;
    left: 0;
  }
  .flasher-note.adjustBtn {
    right: 65%;
    left: auto;
  }
  .flasher-note.adjustBtn .fa {
    left: auto;
    right: 0;
  }
}
</style>
