<template>
  <nav class="navbar navbar-expand navbar-dark bg-dark fixed-nav">
    <header>
      <div class="container">
        <div class="top-banner">
          <div class="contact-info">
            <p class="whatsapp">
              <i class="fa fa-whatsapp" />00963-934-615-642
            </p>
            <p class="email">
              <i class="fa fa-envelope-o" />Rawadj.khair@gmail.com
            </p>
          </div>
          <div class="languages">
            <button
              :class="{ 'active-lang': activeLang === 'en' }"
              @click="changeActiveLang('en', 'ltr')"
            >
              En
            </button>
            <button
              :class="{ 'active-lang': activeLang === 'ar' }"
              @click="changeActiveLang('ar', 'rtl')"
            >
              ع
            </button>
          </div>
        </div>
      </div>
    </header>
    <div class="container">
      <div class="routers">
        <div class="logo" :class="direction === 'ltr' ? '' : 'logo-rtl'">
          <router-link to="/login">
            <img src="/images/icons/logo.png" alt="Logo" class="logo-img" />
          </router-link>
          <h2
            class="logo-title"
            v-if="direction === 'ltr'"
            style=" fontFamily: fantasy"
          >
            <span class="logo-span">E-</span>Commerce
          </h2>
          <h2
            class="logo-title"
            v-if="direction === 'rtl'"
            style=" fontWeight: 'bold'"
          >
            متجـري
          </h2>
        </div>
        <ul class="navbar-nav">
          <li class="nav-item">
            <router-link class="nav-link" to="/">
              <img src="/images/icons/home.png" alt="nav-icon" />
            </router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/products" id="products">
              <img src="/images/icons/products.png" alt="nav-icon" />
            </router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/basket">
              <img src="/images/icons/basket.png" alt="nav-icon" />
            </router-link>
          </li>
        </ul>
        <div
          class="search"
          :class="direction === 'ltr' ? '' : 'search-btn-rtl'"
        >
          <input
            class="form-control"
            type="search"
            :placeholder="langTranslations.data[0].home.search"
            list="searchList"
            @input="e => search(e.currentTarget.value)"
            @blur="e => handleBlur(e)"
            @keypress="e => handleKeyPress(e)"
            @keyup="e => handleKeyUp(e)"
          />
          <datalist>
            <option
              v-for="product in filteredProducts"
              :key="product.id"
              :value="product.title"
            />
          </datalist>
          <router-link
            to="/products"
            class="search-btn"
            id="search"
            :style="
              direction === 'ltr'
                ? { right: '7px' }
                : { left: '7px', right: 'auto' }
            "
          >
            <i class="fa fa-search" />
          </router-link>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Navbar",
  computed: mapGetters([
    "langTranslations",
    "direction",
    "activeLang",
    "filteredProducts"
  ]),
  methods: {
    ...mapActions(["changeLanguage", "search"]),
    changeActiveLang(lang) {
      this.changeLanguage(lang);
    },
    handleBlur(e) {
      e.target.value = "";
    },
    handleKeyPress(e) {
      if (e.which === 13) {
        const search = document.getElementById("search");
        search.click();
      }
    },
    handleKeyUp(e) {
      const searchList = document.getElementsByTagName("datalist")[0];
      if (e.currentTarget.value !== "") {
        searchList.id = "searchList";
      } else {
        searchList.id = "";
      }
    }
  }
};
</script>

<style scoped>
nav {
  padding: 0;
  position: fixed;
  width: 100%;
  max-height: 100px;
  z-index: 1111;
  display: flex;
  flex-direction: column;
  background-color: #484b4d;
}
@media (max-width: 768px) {
  nav {
    min-height: 125px !important;
  }
}
@media (max-width: 500px) {
  nav {
    min-height: 100px !important;
  }
}
.flasher {
  animation: flasher 10s infinite linear;
}
@keyframes flasher {
  0% {
    filter: brightness(1);
  }
  65% {
    filter: brightness(1);
    transform: scale(1);
  }
  70% {
    filter: brightness(1.2);
    transform: scale(1.1);
  }
  75% {
    filter: brightness(1);
    transform: scale(1);
  }
  82% {
    filter: brightness(1.4);
    transform: scale(1.1);
  }
  90% {
    filter: brightness(1);
    transform: scale(1);
  }
}
header {
  width: 100%;
  height: 30px;
  background-color: #afafaf;
  color: white;
  border: 1px solid white;
  user-select: none;
}
.top-banner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}
.contact-info {
  display: flex;
  height: 30px;
}
p {
  margin: 0 2px;
  direction: ltr;
  font-size: 0.8em;
  line-height: 28px;
  height: 100%;
}
.fa-whatsapp {
  color: green;
  font-size: 1.15em;
  margin: 0 5px;
}
.fa-envelope-o {
  color: red;
  font-size: 1.1em;
  margin: 0 5px;
}
@media (max-width: 500px) {
  p {
    font-size: 0.6em;
  }
  .fa-whatsapp {
    font-size: 0.8em;
  }
  .fa-envelope-o {
    font-size: 0.8em;
  }
}
button {
  background-color: transparent;
  margin: 0 2px;
  padding: 0 5px;
  cursor: pointer;
  outline: none;
}
button.active-lang {
  color: white;
  border-style: inset;
}
@media (max-width: 768px) {
  .languages {
    position: fixed;
    top: 35px;
    right: 10px;
  }
}

.routers {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
}
ul {
  justify-content: flex-end;
  margin: 0 25px !important;
  flex: 1;
}
@media (max-width: 768px) {
  ul {
    justify-content: center;
  }
}
.logo {
  display: flex;
  height: 70px;
}
.logo:hover {
  filter: brightness(1.1);
}
.logo-img {
  display: block;
  height: 70px;
}
.logo-title {
  font-size: 1.4em;
  margin-left: 5px;
  line-height: 50px;
  letter-spacing: 1.2px;
  align-self: center;
  text-indent: 5px;
  color: white;
}
.logo-span {
  color: #fc6e51;
  letter-spacing: 3px;
}
@media (max-width: 768px) {
  .logo {
    position: fixed;
    left: 2px;
    top: 75px;
  }
  .logo-rtl {
    right: 10px;
    left: auto;
  }
  .logo-title {
    font-size: 1.2em;
  }
}
.links-list {
  justify-content: flex-end;
  margin: 0 25px !important;
  flex: 1;
}
@media (max-width: 768px) {
  .links-list {
    justify-content: center;
  }
}
li:hover {
  filter: brightness(1.4);
}
li img {
  height: 50px;
}
@media (max-width: 500px) {
  li img {
    height: 30px;
  }
  .logo {
    top: 80px;
  }
  .logo-img {
    height: 30px;
  }
  .logo-title {
    margin-top: -15px;
    margin-left: 0;
    align-self: flex-start;
    font-size: 0.8rem;
  }
}
.search {
  position: relative;
  align-self: center;
}
input {
  max-width: 250px;
  outline: none !important;
  padding: 5px 17px;
}
input::-webkit-calendar-picker-indicator {
  display: none;
}
input:focus {
  border: none;
  box-shadow: none;
}
.search-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  color: #484b4d !important;
}
@media (max-width: 768px) {
  .search {
    position: fixed;
    top: 95px;
    right: 5px;
  }
  .search-btn-rtl {
    left: 10px;
    right: auto;
  }
  input {
    height: 25px;
    font-size: 0.8em;
  }
  .search-btn {
    right: 15px;
    top: 0 !important;
  }
}
@media (max-width: 500px) {
  .search {
    right: 5px;
    top: 85px;
  }
  .search-btn-rtl {
    right: auto;
  }
  input {
    max-width: 135px;
    padding: 5px 20px;
  }
}
</style>
